import ShopCard from "./ShopCard";

export default {
    name: "Shop",
    components: {
        ShopCard
    },
    data () {
        return {
            shopItems: this.returnArray()
        }
    },
    methods: {
        returnArray() {
            const items = this.$store.state.shop;
            return [...new Set(items)];
        },
        deleteArray() {
            this.shopItems = this.returnArray();
        }
    }
}
